/* ==========================================================================
  CSS RESET
============================================================================= */
html{
    height: 100%;
    overflow-x: hidden;
}
body {
    font-family:'Univers';
    font-size: 18px;
    color: #35424a;
    background-color: #faf9f9;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100%;
    position: relative;
    overflow-x: hidden;
    /*padding-bottom: 129px;*/
}

a,
a:active,
a:visited {
    text-decoration: none;
    color: #35424a;
    transition-duration: 0.4s;
    -webkit-transition-duration: 0.4s;
    -moz-transition-duration: 0.4s;
    cursor: pointer;
}

a:hover,
a:focus,
li.active a,
a.active {
    text-decoration: none;
    color: #00a652;
    outline: none;
    outline-offset: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
}

ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

/* ==========================================================================
  HEADER Top
============================================================================= */

.header-top {
    background: #35424a;
    padding: 20px 0;
}

.header-wrapper.after-login-header .header-top {
    padding: 18px 0 17px;
}

.login-title h1 {
    margin: 0;
    text-align: center;
    font-size: 26px;
    color: #aeadb1;
    text-transform: capitalize;
    font-family:'frontage-condensed-bold';
    padding-top: 1px;
}

.header-wrapper.after-login-header .login-title h1 {
    padding-top: 4px;
}

.main-menu {
    float: right;
    padding-top: 3px;
}

.main-menu ul li {
    display: inline-block;
    margin-right: 25px;
}

.main-menu ul li:last-child {
    margin-right: 0;
}

.main-menu ul li a {
    color: #aeadb1;
    font-size: 18px;
    font-family:'frontage-condensed-bold';
}

.main-menu ul li a:hover,
.main-menu ul li a.active {
    color: #8dc63f;
}

.header-wrapper.after-login-header .offcenvas-menu {
    display: block;
    position: relative;
    z-index: 10;
    text-align: left;
    line-height: 44px;
    transition: all ease-in-out 0.5s;
    opacity: 1;
    visibility: visible;
}

.header-wrapper.after-login-header .offcenvas-menu.open {
    opacity: 0;
    visibility: hidden;
}

.header-wrapper.after-login-header .offcenvas-menu > a {
    width: 20px;
    height: 16px;
    background: url('../images/mobile-menu-icon.png') no-repeat;
    display: inline-block;
}

.header-wrapper.after-login-header .offcenvas-menu.open > a {
    background: url('../images/icon-close.png') no-repeat;
}

.header-wrapper.after-login-header .main-menu {
    padding: 95px 20px 0 19px;
    position: fixed;
    height: 100%;
    max-height: 100%;
    background-color: #29353c;
    z-index: 10;
    top: 0;
    width: 250px;
    left: 0;
    transform: translateX(-400px);
    -webkit-transform: translateX(-400px);
    -moz-transform: translateX(-400px);
    transition-duration: 0.4s;
    -webkit-transition-duration: 0.5s;
    -moz-transition-duration: 0.5s;
    transition-delay: 0.2s;
    -webkit-transition-delay: 0.2s;
    -moz-transition-delay: 0.2s;
    border-right: 1px #35424a solid;
}

.header-wrapper.after-login-header .main-menu ul li {
    display: block;
    margin-right: 0;
    border-top: 1px #3b4952 solid;
}

.header-wrapper.after-login-header .main-menu ul li a {
    display: block;
    font-size: 21px;
    padding: 13px 0;
}

.header-wrapper.after-login-header .main-menu.slide-left {
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
}

.header-wrapper.after-login-header .main-menu {
    padding: 15px 30px 0 19px;
    width: 390px;
}

.header-wrapper.after-login-header .main-menu > ul {
    max-width: 235px;
    margin-left: auto;
}

.desktop-menu-header {
    padding-bottom: 17px;
    max-width: 235px;
    margin-left: auto;
}

.desktop-menu-header:after {
    content: '';
    clear: both;
    display: table;
}

.desktop-menu-header > a.close-menu {
    float: left;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    margin-top: 3px;
}

.desktop-menu-header > a.logout {
    float: right;
    padding: 10px 0;
    color: #aeadb1;
    font-size: 16px;
    font-family: 'frontage-condensed-bold';
}

.desktop-menu-header > a > span {
    float: right;
    margin-left: 15px;
}

.header-wrapper.after-login-header .logo {
    float: right;
}

/* ==========================================================================
  HEADER Bottom
============================================================================= */

.header-bottom {
    background: #8ec63f;
    padding: 35px 0 37px;
}

.header-bottom h2 {
    float: left;
    font-size: 40px;
    color: #ffffff;
    margin: 0;
    font-family:'Univers Bold';
}

.header-bottom .back-btn {
    float: right;
}

.header-bottom .header-bottom-setting {
    float: right;
    margin-top: 9px;
}

.header-bottom.align-center h2 {
    float: none;
    text-align: center;
}

.header-bottom .back-btn a {
    font-family:'frontage-condensed-bold';
    display: inline-block;
    width: 92px;
    height: 45px;
    border: 2px #ffffff solid;
    color: #ffffff;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    line-height: 41px;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
}

.header-bottom .back-btn a:hover {
    background: #fff;
    color: #8ec63f;
}

/* ==========================================================================
  Footer
============================================================================= */

.footer-wrapper {
    background: #35424a;
    padding: 47px 0;
    width: 100%;
    bottom: 0;
}

.footer-links {
    text-align: center;
    padding-top: 4px;
}

.footer-links ul li {
    display: inline-block;
    padding: 0 7px 0 5px;
    border-right: 1px #afafb4 solid;
    line-height: 14px;
}

.footer-links ul li:last-child {
    padding-right: 0;
    border-right: none;
}

.footer-links ul li a {
    color: #afafb4;
    font-size: 14px;
}

.footer-links ul li a:hover,
.footer-links ul li a.active {
    color: #8dc63f;
}

.footer-social {
    text-align: right;
    padding-top: 4px;
}

.footer-social a {
    color: #afafb4;
    font-size: 20px;
    margin-left: 15px;
}

.footer-social a:hover {
    color: #8dc63f;
}

.footer-social a:first-child {
    margin-left: 0;
}

/* ==========================================================================
  Login
============================================================================= */

.login-section {
    padding: 60px 10px 55px;
    max-width: 360px;
    margin: 0 auto;
}

.mobile-logo {
    display: none;
}

.offcenvas-menu {
    line-height: 40px;
    width: 40px;
    height: 40px;
    display: none;
}

.form-control {
    height: 60px;
    box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
    -webkit-box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
    border: none;
    font-family:'Univers';
    font-size: 18px;
}

select.form-control {
    opacity: 0.6;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: #72757c;
    opacity: 0.5;
}
::-moz-placeholder { /* Firefox 19+ */
    color: #72757c;
    opacity: 0.5;
}
:-ms-input-placeholder { /* IE 10+ */
    color: #72757c;
    opacity: 0.5;
}
:-moz-placeholder { /* Firefox 18- */
    color: #72757c;
    opacity: 0.5;
}

.form-control:focus {
    border-color: transparent;
    outline: 0;
    box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
    -webkit-box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
}

.form-group.checkbox-cus {
    margin: 54px 0 61px;
}

.form-group.checkbox-cus label {
    margin-bottom: 0;
    display: block;
    position: relative;
    padding-left: 56px;
    cursor: pointer;
    font-size: 18px;
    color: #6b7c86;
    padding-top: 7px;
    font-weight: normal;
    font-family: 'Univers';
}

.control input {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.control__indicator {
    position: absolute;
    top: 2px;
    left: 0;
    width: 30px;
    background: #ffffff;
    height: 30px;
    box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
    -webkit-box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 3px 30px rgba(0,0,0,0.2);
}

.control--checkbox .control__indicator:after {
    content: '';
    position: absolute;
    display: none;
    left: 0;
    top: 0;
    width: 16px;
    height: 12px;
    background: url('../images/icon-right-green.png') no-repeat;
    right: 0;
    bottom: 0;
    margin: auto;
}

.control input:checked ~ .control__indicator:after {
    display: block;
}

.form-btn {
    margin-bottom: 62px;
}

.form-btn button {
    display: block;
    height: 55px;
    border: 2px #1aad4f solid;
    width: 100%;
    background: transparent;
    font-size: 20px;
    color: #00a651;
    border-radius: 4px;
    text-transform: uppercase;
    font-family:'frontage-condensed-bold';
    transition: ease-out 0.5s;
    -webkit-transition: ease-out 0.5s;
    -webkit-transition: ease-out 0.5s;
}

.form-btn button:hover {
    background: #00a651;
    color: #fff;
}

.form-btn button:focus {
    outline: none;
}

.register-acc {
    margin-bottom: 20px;
}

.register-acc,
.forgot-pwd {
    font-size: 18px;
    color: #6b7c86;
}

.register-acc a,
.forgot-pwd a {
    color: #1aad4f;
    font-family:'Univers Bold';
}

/* ==========================================================================
  Registration Section
============================================================================= */

.registration-section {
    max-width: 730px;
    margin: 0 auto;
    padding: 60px 15px 90px;
}

.social-login {
    margin-bottom: 43px;
}

.login-fb {
    padding: 12px;
