.registration-row {
    margin-bottom: 60px;
}

.registration-row.mb-50 {
    margin-bottom: 50px;
}

.registration-section h3 {
    font-size: 18px;
    color: #72757c;
    margin-top: 0;
    margin-bottom: 20px;
    font-family:'Univers Bold';
}

.form-date-block {
    position: relative;
}

.form-date-block span {
    font-size: 18px;
    color: #72757c;
    position: absolute;
    top: -38px;
    opacity: 0.6;
}

.form-date-block:after {
    content: '';
    display: table;
    clear: both;
}

.form-date-block .form-control.dd-date {
    width: 95px;
    margin-right: 15px;
