.already-have-acc {
    text-align: center;
    color: #72757c;
    font-size: 18px;
}

.already-have-acc a {
    color: #00a652;
    font-family:'Univers Bold';
}

.form-terms-condition {
    margin-bottom: 80px;
}

.form-terms-condition:after {
    content: '';
    display: table;
    clear: both;
}

.form-terms-condition .form-group.checkbox-cus {
    margin: 0;
    width: 73%;
    float: left;
    padding-right: 15px;
}

.form-terms-condition .form-group.checkbox-cus label {
    font-size: 14px;
    padding-top: 0;
    color: #72757c;
    padding-left: 45px;
}

.form-terms-condition .form-btn {
    margin: 0;
    float: right;
    width: 185px;
}

