/* ==========================================================================
  Selection Container
============================================================================= */

.selection-container {
    background: #1aad4f;
    padding: 17px 0 16px;
}

.selection-text {
    color: #fff;
    font-size: 14px;
    font-family:'Univers';
    line-height: 22px;
    margin-top: -4px;
}

.selection-text span {
    display: block;
    font-size: 30px;
    font-family:'Univers-Light-Normal';
}

.selection-filter {
    position: relative;
    margin-top: 2px;
}

.selection-filter:after {
    content: '';
    width: 38px;
    height: 40px;
    background: url('../images/select-box-bg.png') no-repeat;
    position: absolute;
    right: 0;
}

.selection-filter select {
    float: right;
    font-size: 16px;
    color: #6b7c86;
    height: 40px;
    padding: 0 39px 0 15px;
    border: none;
    background: #fff;
    -webkit-appearance: none;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    min-width: 160px;
}

.selection-filter select:focus {
    outline: none;
}

/* ==========================================================================
